body {
font-family: "Roboto", sans-serif;
box-sizing: border-box;

}

/*TOP MENU*/
.appbar{
  height: 70px;
  max-width: 67vw;
  margin: 0 auto;
}

nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1vh 0;
}

.appbar h2{
  font-style: normal;
  font-weight: 900;
  font-size: 26px;
  line-height: 38px;
  color: #37447E;
}
.appbar h2 a{
  text-decoration: none;
}
.appbar ul{
  list-style: none;
  width: 30%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.appbar ul li a{
  text-decoration: none;
  color: #505F98;
}
.appbar ul li a img{
  width: 2vw;
  height: 3vh;
}
.appbar ul li button{
  width: 7vw;
  height: 3vh;
  border: none;
  outline: none;
  font-size: 1.4vh;
  background-color: #FFD166;
  border-radius: .2vh;
  color: #091133;
  font-weight: 500;
  line-height: 2vh;
  cursor: pointer;
}

/*HEADER*/
.wrapper{
  width: 67vw;
  margin: 0 auto;
  height: 70vh;
}

.header{
 display: flex;
 align-items: center;
 max-width: 67vw;
 margin: 0 auto;
}


.header .hero-image{
  position: relative;
  float: left;
  width: 50%;
}

.header .hero-image img{
  width: 100%;
}

.header .hero-content{
  margin-left: 4vh;
  float: right;
  width: 50%;
}

.header .hero-content h1{
  line-height: 7vh;
  color: #091133;
}

.header .hero-content ul{
  color: #505F98;
  line-height: 3.5vh;
  max-width: 80%;
}

.hero-mint{
  display: flex;
  justify-content: center;
  padding: 3vh 0;
}
.hero-mint button{
  border: none;
  outline: none;
  width: 100%;
  height: 5vh;
  font-size: 2vh;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  background-color: #091133;
  color: #fff;

}

/* MINTING PROCESS */
.minting_process{
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
}
.minting_process img{
  width: 50%;
  height: 100%;
}
.minting_process div{
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.minting_process div label{
  font-size: 2.2vh;
  line-height: 4vh;
}
.minting_process div label h4{
  color: #37447E;
}


/*ABOUT THE ART*/

.about {
  text-align: center;
  line-height: 3vh;
  max-width: 67vw;
  margin:    0 auto;

}

.about h3 {
  color: #091133;
  margin-top: 20vh ;
  font-weight: 500;
  font-size: 4vh;
  line-height: 5vh;
}

.about ul {
  margin: 4vh auto;
  height: 10vh;
  top: 64px;
  line-height: 3vh;
  color: #505F98;
}

/*ABOUT THE ART - FEATURES*/
.features{
  display: flex;
  align-items:center;
  max-width: 67vw;
  margin:0 auto;
}

.left_text {
  max-width: 33%;
  float: left;
  margin-left: 6vw;
}

.left_text li{
  list-style-type: none;
  margin-top: 4vh;
}

.left_text  img{
  max-height: 3vh;
}

.left_text  h4, .right_text  h4{
  color: #091133;
  font-weight: 500;
  font-size: 1.8vh;
  line-height: 3vh;
}

.left_text  p, .right_text  p{
  color: #5D6970;
  font-size: 1.4vh;
  line-height: 2vh;
  max-width: 90%;
}

.feature_img {
  display: inline-block;
  width: 33.333%;
}

.feature_img img{
  width: 100%;
}

.right_text {
  float: right;
  width: 33.333%;
  margin-left: 4vh;
}

.right_text li{
  list-style-type: none;
  margin-top: 4vh;
}

.right_text  img{
  max-height: 3vh;
}

/*ABOUT THE MINTING AND SETUP*/

.minting-container{
  margin:30vh auto;
  max-width: 67vw;
}

.minting{
  display: flex;
  max-height: 50vh;
  max-width: 67vw;
  margin: 0 auto;
}

.minting .image{
  width: 50%;
  float: left;
}

.minting .image img {
  max-width: 100%;
}

.minting .text{
  background: #FFF6E0;
  mix-blend-mode: normal;
  width: 50%;
  float: right;

}

.minting .text h4{
  color: #091133;
  font-weight: 500;
  font-size: 3.8vh;
  line-height: 5vh;
  margin-left: 5vw;
  margin-top: 15vh;
  margin-bottom: 3vh;
}

.minting .text p{
  color: #6F7CB2;
  font-size: 1.8vh;
  line-height: 2.8vh;
  margin-left: 5vw;

}

.setup{
  display: flex;
  max-height: 50vh;
}

.setup .image {
  width: 50%;
  float: left;
  overflow:hidden;/* crops/cut off */
}

.setup .image img {
  max-width: 100%;
}


.setup .text{
  width: 50%;
  float: right;
  background: #FFF6E0;
  mix-blend-mode: normal;

}

.setup .text h4{
  color: #091133;
  font-weight: 500;
  font-size: 3.8vh;
  line-height: 5vh;
  margin-left: 5vw;
  margin-top: 15vh;
}

.setup .text p{
  color: #6F7CB2;
  font-size: 1.8vh;
  line-height: 2.8vh;
  margin-left: 5vw;
  margin-top: 2vh;
  margin-right: 1vw;
}

/*ABOUT THE TEAM*/

.team{
  max-width: 67vw;
  margin: 10vh auto;
}
.team h3{
  margin: 5vh 0;
  font-weight: 500;
  font-size: 3.8vh;
  line-height: 5vh;
}
.team h4{
  margin: 0 40% 7vh 0;
  font-style: normal;
  font-weight: normal;
  font-size: 1.8vh;
  line-height: 2.8vh;
  color: #6F7CB2;
}

.team ul{
  max-width: 67vw;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  list-style: none;
}
.team ul li{
  width: 25%;
}
.team ul li img{
  max-width: 100%;
  max-height: 25vh;
  margin-bottom:1vh ;
}
.team ul li h5{
  margin-bottom: 1vh;
  font-weight: 500;
  font-size: 1.8vh;
  line-height: 2.8vh;
}

.team ul li h5 a{
    text-decoration: none;
    color: #505F98;
}

.team ul li p{
  color: #76848C;
  font-style: normal;
  font-weight: normal;
  font-size: 1.4vh;
  line-height: 2vh;
  max-width:14vw;
}

/*ABOUT THE DONATIONS*/

.donation{
  background-color: #FFF6E0;
  max-width: 67vw;
  height: 50vh;
  margin: 0 auto;
  margin-top: 10vh;
  display: flex;
}
.donation .don-desc{
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin:    0 auto;
}
.donation .don-img {
  max-width: 50%;
  margin:    0 auto;
  overflow:hidden;
}

.don-desc h4{
    width: 50%;
    margin: 3vh auto;
    font-weight: 500;
    font-size: 3.8vh;
    line-height: 5vh;
    color: #091133;
}
.don-desc p{
    width: 50%;
    margin: 0 auto;
    font-style: normal;
    font-weight: normal;
    font-size: 1.8vh;
    line-height: 2.8vh;
    color: #6F7CB2;
}

.ftr{
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 20vh;
}
.ftr ul{
  display: flex;
  justify-content: space-around;
  width: 20%;
  margin: 0 auto;
  list-style: none;
}
.ftr ul li a{
  text-decoration: none;
  font-style: normal;
  font-weight: normal;
  font-size: 1.6vh;
  line-height: 2.6vh;
  color: #929ECC;
}
.ftr p{
  text-align: center;
  margin: 3vh 0;
  font-style: normal;
  font-weight: normal;
  font-size: 1.4vh;
  line-height: 2vh;
  color: #CDD1D4;
}


/* ---------Mobile Styling--------------- */


/* TOP MENU */
.appbar.mob{
  max-width: 90vw;
  width: 100%;
}
.wrapper.mob{
  max-width: 90vw;
  height: 100%;
}
.appbar.mob h2{
  font-size: 20px;
}
.appbar.mob ul{
  width: 100%;
}
.appbar.mob ul li{
  margin: 0;
}
.appbar.mob ul li a img{
  width: 5vw;
  height: 3vh;
}
.appbar.mob ul li button{
  width: fit-content;
  padding: 0 1vh;
  height: 3vh;
  font-size: 12px;
  line-height: 2vh;
}
/*HEADER*/
.header-mob{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.hero-image.mob{
  width: 100%;
  display: flex;
  justify-content: center;
}
.hero-image.mob img{
  width: 90%;
}
.hero-content.mob{
  margin-top: 3vh;
  margin-left: 6vw;
}
.hero-content.mob h1{
  line-height: 7vh;
}

.hero-content.mob ul{
  line-height: 4vh;
  max-width: 100%;
}
.hero-mint.mob{
  width: 100%;
  padding: 3vh 0;
}

.hero-mint.mob button{
  width: 100%;
  height: 5vh;
  font-size: 3vh;
  background-color: #37447E;
  color: #fff;
}


/* MINTING PROCESS */
.minting_process.mob{
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
.minting_process.mob img{
  width: 100%;
  height: 100%;
}
.minting_process.mob div{
  width: 90%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.minting_process.mob div label{
  width: 100%;
  font-size: 2.2vh;
  line-height: 4vh;
}



/*ABOUT THE ART*/

.about.mob {
  max-width: 100vw;
  margin:0;
}

.about.mob h3 {
  font-size: 36px;
  line-height: 10vh;
}

.about.mob ul {
  width: 90vw;
  text-align: start;
  height: 10vh;
  top: 0;
  line-height: 4vh;
  margin-left: 6vw;
}

/*ABOUT THE ART - FEATURES*/
.features.mob{
  display: flex;
  align-items:center;
  flex-direction: column;
  max-width: 100vw;
  width: 100%;
  margin:0;
}

.left_text.mob,.right_text.mob {
  max-width: 100%;
  width: 90%;
  float: unset;
  margin-left: 6vw;
}

.left_text.mob  p, .right_text.mob p{
  max-width: 100%;
}

.feature_img.mob {
  display: inline-block;
  width: 90%;
  margin: 5vh;
}
.feature_img.mob img{
  width: 100%;
}

/* MINTING */

.minting-container.mob{
  margin: 30vh 0;
  max-width: 100vw;
}

.minting.mob{
  display: flex;
  flex-direction: column;
  max-height: 100%;
  max-width: 100vw;
  margin: 0;
}

.minting.mob .image, .setup.mob .image{
  width: 90%;
  margin: 0 auto;
  float: left;
}

.minting.mob .image img, .setup.mob .image img {
  max-width: 100%;
}

.minting.mob .text, .setup.mob .text{
  width: 90%;
  margin: -1vh auto;
  float: left;
}

.minting.mob .text h4, .setup.mob .text h4{
  margin-left: 6vw;
  margin-top: 5vh;
  margin-bottom: 3vh;
}

.minting.mob .text p, .setup.mob .text p{
  line-height: 26px;
  margin-left: 6vw;
  margin-bottom: 3vh;
}

.setup.mob{
  display: flex;
  flex-direction: column-reverse;
  max-height: 100%;
}

/* TEAM */
.team.mob ul{
  max-width: 100vw;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.team.mob{
  max-width: 100vw;
  margin: 0;
}

.team.mob h3{
  margin: 5vh 3vh;
}

.team.mob h4{
  margin: 0 3vh;
}

.team.mob ul li{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 5vh 0;
}
.team.mob ul li img{
  max-width: 100%;
}
.team.mob ul li h5{
  margin-bottom: 1vh;

}
.team.mob ul li p{
  max-width: 90%;
}

/* DONATION */

.donation.mob{
  max-width: 90vw;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.donation.mob .don-desc{
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 3vh;
}
.donation.mob .don-img {
  max-width: 100%;
  margin: 0 auto;
}
.donation.mob .don-img img{
  width: 100%;
  object-fit: contain;
}

.don-desc.mob h4{
    width: 100%;
    margin: 3vh auto;
}
.don-desc.mob p{
    width: 100%;
    margin: 2vh auto;
}

/* FOOTER */
.ftr.mob{
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 20vh;
}
.ftr.mob ul{
  width: 100%;
  margin: 0;
}

.ftr.mob p{
  margin: 3vh 0;
}


